"use client";

import React, { useState } from "react";

interface ImageAnalysis {
  style: string;
  colors: string[];
  category: string;
  suggestions: {
    type: string;
    description: string;
  }[];
}

interface ImageUploadProps {
  onImageSelect: (file: File) => void;
  selectedImage: File | null;
  onAnalysisComplete?: (analysis: ImageAnalysis) => void;
}

export default function ImageUpload({ 
  onImageSelect, 
  selectedImage,
  onAnalysisComplete 
}: ImageUploadProps) {
  const [isAnalyzing, setIsAnalyzing] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const handleFileChange = async (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (!file) return;

    try {
      setIsAnalyzing(true);
      setError(null);
      onImageSelect(file);

      // Create form data for the API request
      const formData = new FormData();
      formData.append('image', file);

      // Call the image analysis API
      const response = await fetch('/api/image-analysis', {
        method: 'POST',
        body: formData,
      });

      if (!response.ok) {
        throw new Error('Failed to analyze image');
      }

      const data = await response.json();
      if (data.success && onAnalysisComplete) {
        onAnalysisComplete(data.analysis);
      }
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to analyze image');
      console.error('Image analysis error:', err);
    } finally {
      setIsAnalyzing(false);
    }
  };

  return (
    <section className="mb-8">
      <h2 className="text-xl font-semibold mb-2">Upload Your Image</h2>
      <div className="border-2 border-dashed border-gray-400 p-6 rounded-lg text-center">
        <input
          type="file"
          accept="image/*"
          onChange={handleFileChange}
          className="hidden"
          id="image-upload"
        />
        <label
          htmlFor="image-upload"
          className="cursor-pointer inline-block px-4 py-2 bg-black text-white rounded hover:bg-gray-800 transition"
        >
          Choose Image
        </label>
        <p className="mt-2 text-sm text-gray-500">
          Upload a photo to get personalized suggestions
        </p>
      </div>

      {error && (
        <div className="mt-4 p-3 bg-red-100 text-red-700 rounded">
          {error}
        </div>
      )}

      {selectedImage && (
        <div className="mt-4">
          <div className="relative">
            <img
              src={URL.createObjectURL(selectedImage)}
              alt="Uploaded"
              className="max-w-full rounded-lg shadow"
            />
            {isAnalyzing && (
              <div className="absolute inset-0 bg-black bg-opacity-50 flex items-center justify-center rounded-lg">
                <div className="text-white flex items-center space-x-2">
                  <div className="animate-spin">◌</div>
                  <span>Analyzing...</span>
                </div>
              </div>
            )}
          </div>
        </div>
      )}
    </section>
  );
}
