"use client";

import React, { useState } from "react";
import Chat from "@/components/Chat";
import ImageUpload from "@/components/ImageUpload";

interface Message {
  role: 'user' | 'assistant';
  content: string;
}

interface ImageAnalysis {
  style: string;
  colors: string[];
  category: string;
  suggestions: {
    type: string;
    description: string;
  }[];
}

interface Product {
  id: string;
  title: string;
  description: string;
  price: string;
  image: string;
  url: string;
  platform: 'amazon' | 'flipkart';
}

export default function AIAssistantPage() {
  const [chatInput, setChatInput] = useState("");
  const [chatMessages, setChatMessages] = useState<Message[]>([]);
  const [selectedImage, setSelectedImage] = useState<File | null>(null);
  const [isLoading, setIsLoading] = useState(false);
  const [products, setProducts] = useState<Product[]>([]);

  // Handler for sending chat message to AI
  const sendMessage = async () => {
    if (!chatInput.trim() || isLoading) return;

    try {
      setIsLoading(true);
      // Add user message to chat
      const userMessage: Message = { role: 'user', content: chatInput };
      setChatMessages(prev => [...prev, userMessage]);
      setChatInput("");

      // Call chat API
      const response = await fetch('/api/chat', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ message: chatInput }),
      });

      if (!response.ok) {
        throw new Error('Failed to get AI response');
      }

      const data = await response.json();
      
      // Add AI response to chat
      const aiMessage: Message = { 
        role: 'assistant', 
        content: data.message 
      };
      setChatMessages(prev => [...prev, aiMessage]);

    } catch (error) {
      console.error('Chat error:', error);
      setChatMessages(prev => [
        ...prev,
        { 
          role: 'assistant', 
          content: 'Sorry, I encountered an error. Please try again.' 
        }
      ]);
    } finally {
      setIsLoading(false);
    }
  };

  // Handler for image analysis completion
  const handleAnalysisComplete = async (analysis: ImageAnalysis) => {
    try {
      // Add analysis results to chat
      setChatMessages(prev => [
        ...prev,
        {
          role: 'assistant',
          content: `I analyzed your image! I see it's a ${analysis.style} style ${analysis.category}. Let me find some matching products for you.`
        }
      ]);

      // Fetch product recommendations
      const response = await fetch('/api/products', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          category: analysis.category,
          style: analysis.style,
          colors: analysis.colors,
        }),
      });

      if (!response.ok) {
        throw new Error('Failed to fetch products');
      }

      const data = await response.json();
      setProducts(data.products);

      // Add product suggestions to chat
      setChatMessages(prev => [
        ...prev,
        {
          role: 'assistant',
          content: "I found some products you might like! Check them out below."
        }
      ]);

    } catch (error) {
      console.error('Product fetch error:', error);
      setChatMessages(prev => [
        ...prev,
        {
          role: 'assistant',
          content: 'Sorry, I had trouble finding product recommendations. Please try again.'
        }
      ]);
    }
  };

  return (
    <main className="min-h-screen bg-white text-black font-sans p-4 max-w-3xl mx-auto">
      <h1 className="text-3xl font-bold mb-6 text-center">AI Shopping Assistant</h1>

      <ImageUpload
        selectedImage={selectedImage}
        onImageSelect={setSelectedImage}
        onAnalysisComplete={handleAnalysisComplete}
      />

      <Chat
        messages={chatMessages}
        inputValue={chatInput}
        onInputChange={setChatInput}
        onSend={sendMessage}
        isLoading={isLoading}
      />

      {products.length > 0 && (
        <section className="mt-8">
          <h2 className="text-xl font-semibold mb-4">Recommended Products</h2>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            {products.map((product) => (
              <div
                key={product.id}
                className="border border-gray-200 rounded-lg p-4 hover:shadow-lg transition"
              >
                <img
                  src={product.image}
                  alt={product.title}
                  className="w-full h-48 object-cover rounded-lg mb-4"
                />
                <h3 className="font-semibold">{product.title}</h3>
                <p className="text-gray-600 text-sm mb-2">{product.description}</p>
                <p className="font-bold mb-2">{product.price}</p>
                <a
                  href={product.url}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="inline-block bg-black text-white px-4 py-2 rounded hover:bg-gray-800 transition"
                >
                  View on {product.platform}
                </a>
              </div>
            ))}
          </div>
        </section>
      )}
    </main>
  );
}
