"use client";

import React from "react";

interface Message {
  role: 'user' | 'assistant';
  content: string;
}

interface ChatProps {
  messages: Message[];
  inputValue: string;
  onInputChange: (value: string) => void;
  onSend: () => void;
  isLoading?: boolean;
}

export default function Chat({ messages, inputValue, onInputChange, onSend, isLoading = false }: ChatProps) {
  return (
    <section>
      <h2 className="text-xl font-semibold mb-2">Chat with AI</h2>
      <div className="border border-gray-400 rounded p-4 h-64 overflow-y-auto mb-4 bg-gray-50">
        {messages.length === 0 && (
          <p className="text-gray-500">Start the conversation by typing below.</p>
        )}
        {messages.map((msg, idx) => (
          <div
            key={idx}
            className={`mb-2 p-2 rounded-lg ${
              msg.role === 'user'
                ? 'bg-black text-white ml-auto max-w-[80%]'
                : 'bg-gray-200 text-black mr-auto max-w-[80%]'
            }`}
          >
            <p className="whitespace-pre-wrap">{msg.content}</p>
          </div>
        ))}
        {isLoading && (
          <div className="flex items-center space-x-2 text-gray-500">
            <div className="animate-bounce">●</div>
            <div className="animate-bounce delay-100">●</div>
            <div className="animate-bounce delay-200">●</div>
          </div>
        )}
      </div>
      <div className="flex gap-2">
        <input
          type="text"
          value={inputValue}
          onChange={(e) => onInputChange(e.target.value)}
          placeholder="Type your message..."
          className="flex-grow border border-gray-400 rounded p-2"
        />
          <button
            onClick={onSend}
            disabled={isLoading || !inputValue.trim()}
            className={`px-4 rounded transition ${
              isLoading || !inputValue.trim()
                ? 'bg-gray-400 cursor-not-allowed'
                : 'bg-black hover:bg-gray-800'
            } text-white`}
          >
            {isLoading ? 'Sending...' : 'Send'}
          </button>
      </div>
    </section>
  );
}
